import { Component, OnInit } from '@angular/core';
import { red } from 'color-name';
import { AngularWaitBarrier } from 'blocking-proxy/built/lib/angular_wait_barrier';
import { HttpClient, HttpEventType } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit {
  title = 'upload-video-AWS-app';
  selectedFiles = null;
  changeDragOverClass = false;

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  dragover(event) {    
    event.preventDefault();
    this.changeDragOverClass = true;
  };
  
  uploadFiles(event) {
    console.log(event);
    event.preventDefault();
    this.changeDragOverClass = false;

    if (event.dataTransfer.items) {
      for (var i = 0; i< event.dataTransfer.items.length; i++) {
          let file = event.dataTransfer.items[i].getAsFile();
          let fileId = file.name +'-'+ file.lastModified;
          
          let formData = new FormData();
          formData.append(fileId, file, file.name );
          debugger
          this.http.post('/api/Videos',formData, {reportProgress: true, observe:'events'}).subscribe(event=> {
              if (event.type === HttpEventType.UploadProgress) {
                console.log('Upload Progress:' + Math.round(event.loaded/event.total * 100) + '%');
              }
              else 
              {
                console.log(event);
              }
          });
      }
    }
  }
}
